import React from 'react';
import { motion } from 'framer-motion'; // framer-motion import
import titleLogo from '../assets/TadpoleDiary_title_logo_main.png'; // 이미지 import

// 애니메이션 변형 정의
const fadeInVariants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1, transition: { duration: 2 } } // 2초 동안 서서히 나타나는 애니메이션
};

function MainImage() {
    return (
        <div className="main-image-container">
            <motion.img
                src={titleLogo}
                alt="Tadpole Diary Title Logo"
                className="main-image"
                initial="hidden"
                animate="visible"
                variants={fadeInVariants}
            />
        </div>
    );
}

export default MainImage;
