import React from 'react';

import tadImage1 from '../assets/tad_intro1.png';
import tadImage2 from '../assets/tad_intro2.png';
import tadImage3 from '../assets/tad_intro3.png';
import { Carousel } from "react-bootstrap";

const images = [tadImage1, tadImage2, tadImage3];

const MobileImageList = () => {
    return (
        <div>
            <Carousel interval={null} style={{ justifyContent: 'flex-start' }}>
                {images.map((image, index) => (
                    <Carousel.Item key={index}>
                        <div>
                            <br/><br/><br/><br/>
                        </div>
                        <img src={image} alt={`Slide ${index + 1}`} style={{ maxWidth: '100%', maxHeight: 'max-content'}} />
                    </Carousel.Item>
                ))}
            </Carousel>
        </div>
    );
};

export default MobileImageList;
