import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Header from './components/Header';
import ImageCarousel from './components/ImageCarousel';
import Footer from './components/Footer';
import MainImage from './components/MainImage';
import backgroundImg from './assets/TadpoleDiary_background.png';
import DownloadButtons from "./components/DownloadButtons";
import ImageList from "./components/ImageList";
import Introduction from './components/Introduction';
import { BrowserView, MobileView } from "react-device-detect";
import MobileHeader from "./components/MobileHeader";
import MobileMainImage from "./components/MobileMainImage";
import MobileDownloadButtons from "./components/MobileDownloadButtons";
import MobileFooter from "./components/MobileFooter";
import MobileImageList from "./components/MobileImageList";
import MobileIntroduction from "./components/MobileIntroduction";

function App() {
    // 현재 섹션을 추적하는 상태 변수
    const [section, setSection] = useState(0);
    // 스크롤 이벤트가 진행 중인지 추적하는 상태 변수
    const [scrolling, setScrolling] = useState(false);

    // 스크롤 이벤트를 처리하는 함수
    const handleScroll = (direction) => {
        if (!scrolling) { // 다른 스크롤 이벤트가 진행 중이 아닐 때만 실행
            setScrolling(true); // 스크롤 이벤트가 진행 중임을 표시
            setSection((prevSection) => {
                // 다음 섹션을 계산하고, 값이 0과 2 사이로 제한되도록 설정
                const nextSection = Math.max(0, Math.min(prevSection + direction, 2));
                // 계산된 섹션으로 부드럽게 스크롤
                window.scrollTo({
                    top: nextSection * window.innerHeight,
                    behavior: 'smooth'
                });
                // 1초 후에 스크롤 이벤트 진행 중 상태 해제
                setTimeout(() => setScrolling(false), 1000);
                return nextSection;
            });
        }
    };

    useEffect(() => {
        // 스크롤 이벤트 리스너 추가
        const onWheel = (e) => {
            e.preventDefault(); // 기본 스크롤 동작을 막음
            if (e.deltaY > 0) {
                handleScroll(1); // 아래로 스크롤
            } else if (e.deltaY < 0) {
                handleScroll(-1); // 위로 스크롤
            }
        };
        window.addEventListener('wheel', onWheel, { passive: false });

        // 컴포넌트 언마운트 시 이벤트 리스너 제거
        return () => window.removeEventListener('wheel', onWheel);
    }, [scrolling]);

    return (
        <div className="App" style={{ overflow: 'hidden' }}>
            <BrowserView>
            <Header />
            <div style={{ height: '300vh', position: 'relative' }}>
                {/* 첫 번째 섹션 */}
                <div className="section" style={{ height: '100vh', backgroundImage: `url(${backgroundImg})`, backgroundSize: 'cover' }}>
                    <MainImage />
                    <DownloadButtons />
                </div>
                {/* 두 번째 섹션 */}
                <div className="section" style={{ height: '100vh', backgroundColor: '#6BD2BB' }}>
                    <ImageList />
                </div>
                {/* 세 번째 섹션 */}
                <div className="section" style={{ height: '100vh', display: 'flex', flexDirection: 'row', backgroundColor: '#ADD8E6' }}>
                    <Introduction />
                    <div className="section-right" style={{ flex: 1.2, display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                        <ImageCarousel />
                    </div>
                </div>
            </div>
            <Footer />
            </BrowserView>
            <MobileView>
                <MobileHeader/>
                <div style={{height: '300vh', position: 'relative'}}>
                    {/* 첫 번째 섹션 */}
                    <div style={{height: '100vh', backgroundImage: `url(${backgroundImg})`, backgroundSize: 'cover'}}>
                        <MobileMainImage/>
                        <MobileDownloadButtons/>
                    </div>
                    {/* 두 번째 섹션 */}
                    <div style={{height: '100vh', backgroundColor: '#6BD2BB'}}>
                        <MobileImageList/>
                    </div>
                    {/* 세 번째 섹션 */}
                    <div style={{height: '100vh', display: 'flex', flexDirection: 'row', backgroundColor: '#ADD8E6'}}>
                        <MobileIntroduction/>
                    </div>
                </div>
                <MobileFooter/>
            </MobileView>
        </div>
    );
}

export default App;
